import { DOCUMENT } from '@angular/common';
import { HttpClient } from '@angular/common/http';
import {
  Component,
  Inject,
  ElementRef,
  OnInit,
  Renderer2,
  HostListener
} from '@angular/core';
import { ROUTES, storeROUTES } from './sidebar-items';
import { empROUTES } from './sidebar-items';
import { adminROUTES } from './sidebar-items';

declare const Waves: any;
@Component({
  selector: 'app-sidebar',
  templateUrl: './sidebar.component.html',
  styleUrls: ['./sidebar.component.sass']
})
export class SidebarComponent implements OnInit {
  constructor(
    @Inject(DOCUMENT) private document: Document,
    private renderer: Renderer2,
    public elementRef: ElementRef,
    private http: HttpClient
  ) {}

mainurls = localStorage.getItem('mainurl');
  posts : any ={};
  username : any ;
  userrole : any ;
  public sidebarItems: any[];
  showMenu: string = '';
  showSubMenu: string = '';
  public innerHeight: any;
  public bodyTag: any;
  listMaxHeight: string;
  listMaxWidth: string;
  headerHeight = 60;


  @HostListener('window:resize', ['$event'])
  windowResizecall(event) {
    this.setMenuHeight();
    this.checkStatuForResize(false);
  }

  @HostListener('document:mousedown', ['$event'])
  onGlobalClick(event): void {
    if (!this.elementRef.nativeElement.contains(event.target)) {
      this.renderer.removeClass(this.document.body, 'overlay-open');
    }
  }

  callMenuToggle(event: any, element: any) {
    if (element === this.showMenu) {
      this.showMenu = '0';
    } else {
      this.showMenu = element;
    }
    const hasClass = event.target.classList.contains('toggled');
    if (hasClass) {
      this.renderer.removeClass(event.target, 'toggled');
    } else {
      this.renderer.addClass(event.target, 'toggled');
    }
  }
  callSubMenuToggle(element: any) {
    if (element === this.showSubMenu) {
      this.showSubMenu = '0';
    } else {
      this.showSubMenu = element;
    }
  }
  id;

  url= this.mainurls +'emp/'


  ngOnInit() {
    this.username = localStorage.getItem('username');
    console.log(this.username);
    this.userrole = localStorage.getItem('userrole');
    this.id = localStorage.getItem('userid');
    console.log(this.id);
    this. getData();

    console.log('this is the menu role' ,this.userrole );
    if(this.userrole =='Admin'){
      console.log(this.userrole );


      this.sidebarItems = adminROUTES.filter(sidebarItem => sidebarItem);
    }
    else if (this.userrole == 'Supervisor') {
      this.sidebarItems = ROUTES.filter(sidebarItem => sidebarItem);

    }
    else if (this.userrole == 'Store Executive' || this.userrole == 'Store Manager' ) {
      this.sidebarItems = storeROUTES.filter(sidebarItem => sidebarItem);

    }else {

      this.sidebarItems = empROUTES.filter(sidebarItem => sidebarItem);

    }


      this.initLeftSidebar();
      this.bodyTag = this.document.body;



  }


  getData() {
    this.http.get(this.url + this.id).subscribe(res => {
      this.posts = res;
      console.log(this.posts );

    // tslint:disable-next-line: triple-equals

    });

  }
  initLeftSidebar() {
    var _this = this;
    //Set menu height
    _this.setMenuHeight();
    _this.checkStatuForResize(true);
  }




  setMenuHeight() {
    this.innerHeight = window.innerHeight;
    var height = this.innerHeight - this.headerHeight;
    this.listMaxHeight = height + '';
    this.listMaxWidth = '500px';
    //Scroll active menu item when page load, if option set = true
    /*  if ($.MyAdmin.options.leftSideBar.scrollActiveItemWhenPageLoad) {
          var activeItemOffsetTop = $('.menu .list li.active')[0].offsetTop
          if (activeItemOffsetTop > 150) $el.slimscroll({ scrollTo: activeItemOffsetTop + 'px' });
      }*/
  }
  isOpen() {
    return this.bodyTag.classList.contains('overlay-open');
  }
  checkStatuForResize(firstTime) {
    if (window.innerWidth < 1170) {
      this.renderer.addClass(this.document.body, 'ls-closed');
    } else {
      this.renderer.removeClass(this.document.body, 'ls-closed');
    }
  }

  mouseHover(e) {
    let body = this.elementRef.nativeElement.closest('body');

    if (body.classList.contains('submenu-closed')) {
      this.renderer.addClass(this.document.body, 'side-closed-hover');
      this.renderer.removeClass(this.document.body, 'submenu-closed');
    }
  }
  mouseOut(e) {
    let body = this.elementRef.nativeElement.closest('body');

    if (body.classList.contains('side-closed-hover')) {
      this.renderer.removeClass(this.document.body, 'side-closed-hover');
      this.renderer.addClass(this.document.body, 'submenu-closed');
    }
  }
}
